import logo from './logo.svg';
import './App.css';
import { NavLink, HashRouter, Switch, Route } from 'react-router-dom'
import Home from './pages/Home/Home';
import About from './pages/About/About'
import NotesSlides from './pages/Notes-Slides/NotesSlides'
import { notesSlides } from './pages/Notes-Slides/NotesSlidesArchive'
import NotesDisplay from './pages/Notes-Slides/NotesDisplay'

function generateNotesRouter(a) {
  return <Route path={`/notes-slides/${a.notes.id}`} 
  component={() => <NotesDisplay id={`${a.notes.id}`}/>}></Route>
}

const notesRouters = notesSlides.map(generateNotesRouter)

const pages = [
  {
    name: "Home",
    link: "/",
    component: Home
  },
  {
    name: "Notes/Slides",
    link: "/notes-slides",
    component: NotesSlides
  },
  {
    name: "About",
    link: "/about",
    component: About
  }
]


function App() {
  return (
    <HashRouter>
    <div className="App">
      <body>
        <div className="headerContainer">
          Physics Undergraduate Journal Club
        </div>
        <div className="sideNav">
          <div className="navItem">
          <NavLink exact to="/" className="navUnselected" activeClassName="navSelected">
            Home
          </NavLink>
          </div>
          <div className="navItem">
          <NavLink exact to="/notes-slides" className="navUnselected" activeClassName="navSelected">
            Notes/Slides
          </NavLink>
          </div>
          <div className="navItem">
          <NavLink exact to="/about" className="navUnselected" activeClassName="navSelected">
            About
          </NavLink>
          </div>
        </div>
        <div className="content">
          <Switch>
            <Route exact path="/notes-slides">
              <NotesSlides/>
            </Route>
            <Route exact path="/about">
              <About/>
            </Route>
            <Route exact path="/">
              <Home/>
            </Route>
            {notesRouters}
          </Switch>
        </div>
      </body>
    </div>
    </HashRouter>
  );
}

export default App;
